import React from "react";
import {connect} from "react-redux";
import {registerMaxMonthlyPriceCMSConf} from "eshop/modules/simfree/actions/metaData";
import {selectMaxMonthlyPrice, reloadProductList} from "eshop/modules/simfree/actions/filter";
import {subscribeToClientContextChange} from "eshop/modules/configurator/actions/filters";
import {getTieredMaxMonthlyPriceForCurrentOfferType,getSelectedMaxMonthlyPrice} from "eshop/modules/simfree/selectors";
import ProductListOneTimeMobileFilterView from "./view/ProductListOneTimeMobileFilterView"
import OnlineUtils from "eshop/utils/OnlineUtils";

class ProductListMaxMonthlyMobileFilterComponent extends React.Component {


    constructor(props) {
        super(props);
        this.labelProvider = this.labelProvider.bind(this);
    }

    labelProvider(row){
        let to = this.props.filterLabelsHardcodedAsGross ? OnlineUtils.formatPrice(OnlineUtils.toGrossHardcoded(row.to)) : row.to
        return row.alias || "Do " + to + " z≈Ç"
    }

    render() {
         return (
             <ProductListOneTimeMobileFilterView
                 tieredPrices={this.props.tieredPrices}
                 initiallyExpanded = {this.props.initiallyExpanded}
                 header = {this.props.title}
                 type = "radio"
                 id="mobile-expander-module-monthly"
                 labelFormatter = {this.labelProvider}
                 selectedTieredPrices={this.props.selectedTieredPrices}
                 onClick={this.props.onClick}
                 showExpanderNumber={this.props.tieredPriceNumberExpander}
                 showComponent={this.props.tieredPrices && this.props.tieredPrices.length > 0 && !this.props.isSalesOfGoodsPage}
                 />
         );
     }
}

export default ProductListMaxMonthlyMobileFilterComponent;

